@media (max-width:1024px) {
  .alignment {
    height: 17vw;
  }

  /* Closed Menu */
  
  .menu {
    display: flex;
    background: #FCFCFC;
    box-shadow: 0px 4px 4px rgba(52, 51, 50, 0.25);
    height: 17vw;
    z-index: 10;
    position: absolute;
    justify-content: center;
    top: 0;
    position: fixed;
    z-index: 10;
    left: 0;
    right: 0;
  }

  .menu .container {
    width: calc(100vw - 32px);
    height: 17vw;
    height: 100%;
    position: relative;
    align-self: center;
  }

  #CitiLogo {
    height: 8.2vw;
    margin-top: 3vw;
  }

  #burguerClosed {
    width: 12vw;
    position: absolute;
    top: 1.5vw;
    right: 0;
    height: 12vw;
  }

  .socialMenu,
  .sectionsMenu,
  #burguerOpen{
    visibility: collapse;
  }

 

  /* Hamburguer Open */
  .menu ul {
    list-style-type: none;
  }

  .sectionsMenu a {
    text-decoration: none;
    margin-right: 16px;
  }

  .menu.change .socialMenu{
    visibility: visible;
    visibility: initial;
    display: flex;
    position: absolute;
    right: 16vw;
    top: 0.5vw;
  }

  #BurguerBubble {
    width: 0;
  }

  #LinkdInIcon,
  #FacebookIcon,
  #InstagramIcon {
    width: 6.4vw;
    height: 6.4vw;
    margin-left: 5vw;
  }
  .menu.change .sectionsMenu{
    visibility: visible;
    visibility: initial;
    z-index: 40;
  }

  .menu.change #burguerOpen{
    visibility: visible;
    width: 12vw;
    position: absolute;
    top: 1.5vw;
    right: 0;
    height: 12vw;
  }

  .menu.change #burguerClosed{
    width: 0px;
  }
  
  .menu.change {
    height: 100%;
    display: flex;
    position: fixed;
    left: 0;
    align-content: center;
  }
  .menu.change #CitiLogo {
    visibility: hidden;
  }
  
  .menu.change .container{
    width: calc(100vw - 32px);
    height: 17vw;
    height: 100%;
    position: relative;
    align-self: center;
    margin: 0;
    padding: 0;
  }

  .sectionsMenu a{
    font-family: 'Barlow', sans-serif;
    font-style: normal;
    font-weight: 501;
    font-size: 4.8vw;
    line-height: 5.2vw;
    text-align: left;
    color: #292929;
    flex-wrap: nowrap;
    z-index: 20;
    right: 0;
    margin-bottom: 3vw;
    float: right;
    display: flex;
    flex-direction: row;
  }

  .menu.change ul{
    font-style: normal;
    font-weight: 501;
    font-size: 4.8vw;
    line-height: 5.2vw;
    text-align: left;
    color: #292929;
    flex-wrap: nowrap;
    z-index: 20;
    right: 0;
    margin-bottom: 3vw;
    display: flex;
    flex-direction: row;
  }
  
  .menu.change .sectionsMenu {
    top: 36vw;
    display: flex;
    flex-direction: column;
    position: absolute;
    right:16px;
    margin-bottom: 3vw;
    height: 75vw;
    justify-content: space-between;
  }



  .menu.change #BurguerBubble {
    width: 100%;
    left: 0;
    bottom: 0;
    position: absolute;
    height: auto;
  }

  .menu.change .line {
    width: 2.7vw;
    height: 0;
    border: 0.5vw solid #292929;
    border-radius: 0.5vw;
    align-self: center;
    background-color: #292929;
    margin-right: 2vw;
  }
}



